// components/ProductCard.tsx
'use client';

import Image from 'next/image';
import NextLink from 'next/link';
import { Heart, Star } from 'lucide-react';
import { useState } from 'react';

export type ProductCardProps = {
  href: string;
  imageSrc: string;         // e.g. "/products/sku-1.jpg" or remote URL (allowed in next.config)
  imageAlt: string;
  title: string;            // e.g. "Relaxed Cotton Blazer"
  sizeLabel?: string;       // e.g. "M - 2XL"
  priceFormatted: string;   // e.g. "₡2500" or "$79.00"
  compareAtFormatted?: string; // e.g. "₡5000" or "$129.00"
  rating?: number;          // e.g. 4.9
  onToggleWishlist?: (next: boolean) => void; // optional callback
  initiallyWishlisted?: boolean;
};

export default function ProductCard({
  href,
  imageSrc,
  imageAlt,
  title,
  sizeLabel,
  priceFormatted,
  compareAtFormatted,
  rating,
  onToggleWishlist,
  initiallyWishlisted = false,
}: ProductCardProps) {
  const [wishlisted, setWishlisted] = useState(initiallyWishlisted);

  function toggleWishlist() {
    const next = !wishlisted;
    setWishlisted(next);
    onToggleWishlist?.(next);
  }

  return (
    <div className="group">
      {/* Image */}
      <div className="relative overflow-hidden rounded-2xl bg-neutral-200">
        <NextLink href={href} aria-label={`${title} details`}>
          <Image
            src={imageSrc}
            alt={imageAlt}
            width={800}
            height={1000}
            className="h-[440px] w-full object-cover transition-transform duration-300 group-hover:scale-[1.03]"
            priority={false}
          />
        </NextLink>

        {/* Wishlist (heart) */}
        <button
          type="button"
          onClick={toggleWishlist}
          aria-label={wishlisted ? 'Remove from wishlist' : 'Add to wishlist'}
          className="absolute right-3 top-3 rounded-full bg-white/90 p-2 shadow hover:bg-white"
        >
          <Heart
            className="h-5 w-5"
            // fill when wishlisted
            fill={wishlisted ? 'currentColor' : 'transparent'}
          />
        </button>
      </div>

      {/* Meta */}
      <div className="mt-3 flex items-start justify-between gap-4">
        <div className="min-w-0">
          <NextLink href={href} className="block">
            <h3 className="truncate text-base font-semibold leading-tight">{title}</h3>
          </NextLink>
          {sizeLabel && (
            <p className="mt-1 text-sm text-neutral-500">{sizeLabel}</p>
          )}
          {typeof rating === 'number' && (
            <div className="mt-1 flex items-center gap-1 text-sm text-neutral-800">
              <span>{rating.toFixed(1)}</span>
              <Star className="h-4 w-4 fill-current" />
            </div>
          )}
        </div>

        {/* Prices */}
        <div className="text-right">
          <div className="text-base font-medium">{priceFormatted}</div>
          {compareAtFormatted && (
            <div className="mt-0.5 text-sm text-neutral-500 line-through">
              {compareAtFormatted}
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
